@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #f5f6fa;
}

main {
  display: flex;
}

.dropdown {
  background-color: white;
  max-width: 200px;
}

.stats {
  width: 40%;
  height: 100vh;
  overflow: auto;
  background: whitesmoke;
  padding: 17px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 14px;
}

.logo {
  height: 50px;
  margin-right: 0.5rem;
}

.header-name {
  display: flex;
  align-items: center;
}

.stats-card {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

::-webkit-scrollbar {
  width: 7px;
  height: 0px;
  border-left: 0;
  background: rgba(0, 0, 0, 0.1);
}
::-webkit-scrollbar-thumb {
  background: #3a3938;
  border-radius: 0;
  height: 37px;
}

.info-box {
  flex: 1 1;
  cursor: pointer;
}

.info-box:not(:last-child) {
  margin-right: 15px;
}

.info-box-cases {
  color: #cc1034;
  font-weight: 600;
  font-size: 1.75rem;
  margin-bottom: 0.5rem;
}

.info-box-selected {
  border-top: 10px solid greenyellow;
}

.info-box-red {
  border-color: red;
}

.info-box-cases-green {
  color: lightgreen;
}

.info-box-grey {
  border-color: grey;
}

.info-box-title {
  font-weight: 600;
}

.info-box-cases-grey {
  color: grey;
}

.info-box-total {
  color: #6c757d;
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  margin-top: 15px !important;
}

.map {
  height: 100vh;
  width: 60%;
}

.info-flag img {
  width: 100px;
  border-radius: 5px;
}

.info-flag {
  height: 80px;
  width: 100%;
  background-size: cover;
  border-radius: 8px;
}

.info-confirmed,
.info-deaths,
.info-recovered {
  font-size: 16px;
  margin-top: 5px;
}

.info-name {
  font-size: 20px;
  font-weight: bold;
  color: #555;
}

.table {
  margin-top: 20px;
  overflow: scroll;
  height: 400px;
  color: #6a5d5d;
  background-color: #fff;
}

.table tr {
  display: flex;
  justify-content: space-between;
}

.table td {
  padding: 0.5rem;
}
.table tr:nth-of-type(odd) {
  background-color: #ebebeb;
}

.table tr:first-child {
  background-color: #3a3938;
  color: lightgray;
}

